<template>
        <div class="goods-content-main" ref="goods-content-main">
            <div v-show="classifyContentData.length>0" >
                <div class="goods-wrap"   v-for="(item,index) in classifyContentData" :key="index">
                    <div class="goods-wrap-title">{{item.title}}</div>
                    <div class="goods-wrap-content">
                        <ul v-for="(item2,index2) in  item.goods" :key="index2" @click="$router.push('/goods/details/item?gid='+item2.gid)">
                            <li><img src="../../assets/images/common/lazyImg.jpg" :data-echo="item2.image"   :alt="item2.title"></li>
                            <li>{{item2.title}}</li>
                        </ul>
                    </div>
                </div>
            </div>
            <div v-show="classifyContentData.length<=0" class="no-data">没有相关商品！</div>
        </div>
</template>

<script>
    import {mapActions,mapState} from "vuex";
    import IScroll from '../../assets/js/libs/iscroll';
    export default {
        methods:{
            //mapActions
            ...mapActions({
                //到处vuex管理的getClassifyContent方法
                getClassifyContent:"goods/getClassifyContent"
            }),
            //禁用默认事件
            eventTouchmoveTop(e){
                e.preventDefault();
            },

            //封装init方法用于getClassifyContent方法
            init(cid){
                //运行getClassifyContent方法
                this.getClassifyContent({cid:cid,success:()=>{
                        this.$nextTick(()=>{
                            this.$utils.lazyImg();
                            this.myScroll.refresh();

                        })
                    }});
            }
        },
        computed:{
            //mapState
            ...mapState({
                //导出classifyContentData的数据
                classifyContentData:(state)=>state.goods.classifyContentData
            })
        },
        created(){
            //获取路由cid
            this.cid = this.$route.query.cid?this.$route.query.cid:"";
            //运行封装方法
            this.init(this.cid);

        },
        mounted(){
            //goods-classify 滑动事件
            this.$refs["goods-content-main"].addEventListener('touchmove', this.eventTouchmoveTop);
            this.myScroll= new IScroll(this.$refs["goods-content-main"], {
                // eventPassthrough : true,
                scrollX : false,
                scrollY : true,
                preventDefault : false
                //click:true
            });
            //滚动结束在进行懒加载 不然部分图片加载不出来
            this.myScroll.on("scrollEnd",()=>{
                this.$utils.lazyImg();
            })
        },
        //路由更新 地址不变使用，
        beforeRouteUpdate(to,from,next){

            //运行封装方法
            this.init(to.query.cid);

            next();
        },
        //路由实例销毁之前调用
        beforeDestroy(){
            this.$refs["goods-content-main"].removeEventListener('touchmove', this.eventTouchmoveTop);
        }
    }
</script>

<style scoped>

    .goods-content-main {
        width: 100%;
        height: 100%;
        overflow: hidden;
        position: relative;
        z-index: 1;
    }
    .goods-content-main .no-data {
        text-align: center;
        font-size: .32rem;
        margin-top: .3rem;
    }
    /*goods-wrap*/
    .goods-wrap {
        width: 100%;
        height: auto;
    }
    .goods-wrap .goods-wrap-title {
        font-size: .28rem;
        width: 100%;
        height: .6rem;
        line-height: .6rem;
        overflow: hidden;
    }
    .goods-wrap .goods-wrap-content {
        width: 100%;
        height: auto;
        background-color: #fff;
        padding-top: .2rem;
        overflow: hidden;
    }
    .goods-wrap-content ul {
        width: 32%;
        height: auto;
        float: left;
        margin-left: .5%;
        margin-right: .5%;
        margin-bottom: .2rem;
    }
    .goods-wrap-content ul li:first-child {
        width: 1.5rem;
        height: 1.5rem;
        overflow: hidden;
        margin: 0 auto;
        text-align: center;
    }
    .goods-wrap-content ul li:first-child img {
        width: 100%;
        height: 100%;
    }
    .goods-wrap-content ul li:last-child {
        width: 90%;
        height: .8rem;
        font-size: .24rem;
        overflow: hidden;
        text-align: center;
        margin: 0 auto;
        white-space: nowrap;
        text-overflow: ellipsis;
        margin-top: .2rem;
    }
</style>